class BirthdayValidator extends Validator {
        validate(){
            super.validate();
    
            if (!this.$field.value.includes('@')){
                this.errors.push(`You must include an @ sign`)
            }
            if (!this.$field.value.includes('.')){
                this.errors.push(`You must include an . sign`)
            }
    
            this.showErrors();
        }
    }